import { Controller } from '@ali/4ever-cangjie';
import { LINEHEIGHT_SET } from '../actions';
import setLineHeight from '../commands/setLineHeight';

export default function onAction(action, controller: Controller, next) {
  const { type, payload } = action;
  if (type === LINEHEIGHT_SET) {
    const { lineHeight, lineRule } = payload;
    return setLineHeight(controller, lineHeight, lineRule);
  }
  return next();
}
